$background_color_nav: #272727;
$color_seconds: #FFD600;

@import "../responsive";


@mixin btn($bg, $text_color){
    background-color: $bg;
    border: 0;
    font-family: Montserrat;
    font-size: 18px;
    font-style: normal;
    font-weight: 700;
    text-align: center;
    color: $text_color;
    padding: 6px 45px;
    cursor: pointer;
}


.header{
    background-color: $background_color_nav;
    color: white;
    position: relative;


   .mobile_elements{
       display: none;
       @include large{
          display: block;
          width: 90%;
          margin: auto;

           .mobile_logo{
               padding-top: 15px;
               text-align: center;
                 li{
                     &:first-of-type{
                         
                     }
                 }
           }
           .bars_shop{
               display: flex;
               align-items: center;
               justify-content: space-between;
               height: 100%;
               padding: 10px 0;

               li{
                   &:first-of-type{
                       cursor: pointer;
                   }
               }
           }
       }
   }

   
    
    .nav_bar{
        display: flex;
        justify-content: space-between;
        align-items: center;
        max-width: 1420px;
        width: 100%;
        height: 110px;
        margin: 0 auto;


        .cancel{
            display: none;
        }

        .program{
            display: none;
        }
        


        @include large{
            display: none;

            &.active_mobile{
                display: block;
                position: fixed;
                background-color: rgba(39, 39, 39, 0.64);
                backdrop-filter: blur(27px);
                top: 0;
                right: 0;
                left: 0;
                bottom: 0;
                max-width: 100%;
                right: 0;
                height: 100vh;
                z-index: 10;


                .cancel{
                    display: block;
                    position: absolute;
                    right: 27px;
                    top: 47px;
                    cursor: pointer;
                }


                .nav_bar_links{
                    display: block;
                    position: absolute;
                    left: 20px;
                    top: 105px;

                    li{
                        margin-bottom: 25px;

                        .links-a{
                            font-size: 20px;

                        }
                    }
                }


                .nav_bar_icons{

                    position: absolute;
                    top: 452px;
                    left: 20px;
                     
                    li{
                        &:first-of-type{
                            display: none;
                        }
                    }
                }


                .program{
                    display: block;
                    position: absolute;
                    left: 20px;
                    top: 536px;

                    li{
                        &:first-of-type{
                            margin-bottom: 25px;
                            font-size: 18px;
                            font-family: sans-serif;
                            font-weight: 200;
                        }
                        &:last-of-type{
                            color: #FFD600;
                            font-size: 18px;
                            font-family: sans-serif;
                            font-weight: 700;
                        }
                    }
                }
           }

            
         }

        

        

        @include desktopExtraExtraLarge{
            max-width: 90%;

        }

        ul{
            display: flex;
            align-items: center;
        }

        .logo{
            display: flex;
            gap: 1rem;

            @include large{
                display: none;
            }

            
 
        }

        .nav_bar_links{
            display: flex;
            gap: 3rem;

            
            @include desktopExtraExtraLarge{
                gap: 1.5rem;
                
               
            }

            

            li{
                

                .links-a{
                    color: white;
                    font-size: 18px;
                    font-weight: 300;
                }

                .active{
                    font-weight: 700;
                }

                
            }
        }


        .nav_bar_icons{
            display: flex;
            gap: 3rem;

            @include desktopExtraExtraLarge{
                gap: 1rem;

            }
           

            li{
                
                color: $color_seconds;
                cursor: pointer;

                i{
                    width: 30px;
                    height: 30px;
                    background-color: $color_seconds;
                    border-radius: 100px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    color: $background_color_nav;
                    font-size: 20px;
                }

            }


          


            
        }



        }


        .wishListCards{
            position: absolute;
            top: 100% !important;
            right: 5%;
            width: 460px;
            background-color: rgba(39, 39, 39, 0.85)  ;
            top: 20%;
            padding: 22px 30px 35px 30px;
            z-index: 999;
            display: flex;
            justify-content: center;
            flex-direction: column;
            

            @include medium{
                position: absolute;
                right: 0;
                width: 70%;

                @include small{
                    width: 60%;
                }
            }
    
    
              .elementWishList{
                display: flex;
                justify-content: space-between;
                align-items: center;
                margin-bottom: 30px;
                
    
    
                h3{
                    font-family: Montserrat;
                    font-size: 18px;
                    font-style: normal;
                    font-weight: 700;
                    color: white;

                    @include small{
                        font-size: 14px;
                    }
                }
    
                button{
                    display: flex;
                    justify-self: center;
                    @include btn(#272727,white );

                    @include small{
                        font-size: 14px;
                        background-color: transparent;

                    }
    
                
                }
    
              }
    
    
              .btn_bottom{
                display: flex;
                align-self: center;
                @include btn($color_seconds, $background_color_nav);
                margin-top: 45px;

                @include small{
                    font-size: 14px;
                    padding: 6px 30px ;
                }
    
              }
    
    
            
        }
        
}



.btn{
    display: none;
    margin: 0;
}





.burger_menu{
    width: 100%;
    height: 100vh;
    background-color: red;
}












